// import React from 'react'
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/esm/Col";
import Row from "react-bootstrap/esm/Row";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
// import NavDropdown from 'react-bootstrap/NavDropdown';
import { BsSearch, BsFillFilePersonFill, BsCartPlus } from "react-icons/bs";
// import ButtonGroup from 'react-bootstrap/ButtonGroup';
// import Button from 'react-bootstrap/Button';
import logo from "../atom/Image/logo.jpeg";
import img from '../atom/Image/img.png';
function Header() {
  return (
    <>
      <Container>
        <Navbar>
          <Navbar.Brand href="#home">
            <img className="logo" src={logo} />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto" style={{ marginLeft: "250px" }}>
              <Nav.Link href="#home">Women</Nav.Link>
              <Nav.Link href="#link">Men</Nav.Link>
              <Nav.Link href="#home">Smart Gear</Nav.Link>
              <Nav.Link href="#link">Accessories</Nav.Link>
            </Nav>
            <Nav className="me-auto" style={{ marginLeft: "250px" }}>
              <Nav.Link href="#home">
                <BsSearch />
                <span className="ps-2">Search</span>
              </Nav.Link>
              <Nav.Link href="#link">
                <BsFillFilePersonFill />
                <span className="ps-2">signin</span>
              </Nav.Link>
              <Nav.Link href="#home">
                <BsCartPlus />
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      <div>
        <Row>
          <Col md={6} className="Women">
           
              <div className="Women1">Women's</div>
           
          </Col>
          <Col md={6} >
            <img className="img" src={img} />
          </Col>
        </Row>
      </div>
      </Container>
    </>
  );
}

export default Header;
